.imagecompare {
  width: 100%;
  position: relative;
  overflow: hidden;
  cursor: col-resize;
}

.imagecompare img {
  display: block;
  width: 100%;
  height: 100%;
}

.imagecompare .comparator {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 50%;
  overflow: hidden;
}

.imagecompare .divider {
  position: absolute;
  left: 50%;
  top: 0;
  bottom: 0;
  width: 4px;
  margin-left: -2px;
  background: rgba(0, 0, 0, 0.5);
}
